import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import * as serviceWorker from "./serviceWorker";
import { ThemeProvider } from "./themeContext"; // 引入 ThemeProvider

// 将 ThemeProvider 包裹整个应用
ReactDOM.render(
  <ThemeProvider>
    <App />
  </ThemeProvider>,
  document.getElementById("root")
);

// 如果你希望应用在离线时工作并加速加载，可以将 unregister() 改为 register()。
// 注意：这有一些潜在的风险。
// 了解更多关于 service worker 的信息：https://bit.ly/CRA-PWA
serviceWorker.unregister();
